import React from 'react';
import SNSTableRow from './SNSTableRow.js';
import Button from '@material-ui/core/Button';
import {openUrl} from './Browser';

function FacebookRowButtons() {

  const openFacebook = () => {
    openUrl('https://facebook.com');
  }
  return (
    <SNSTableRow title="Facebook"
      openButton={
      (<Button onClick={openFacebook}>
        Open
      </Button>)}

    >
    </SNSTableRow>
  )
}

export default FacebookRowButtons;
