import React, {useState} from 'react';
import SNSTableRow from './SNSTableRow.js';
import Button from '@material-ui/core/Button';
import {openUrl} from './Browser';
import MastodonDialog from './MastodonDialog';

const defaultMastodon = 'https://g0v.social'
const mastodonEncodedLengthLimit= 700;

function MastodonRowButtons({message = ''}) {

  const encodedMessage = encodeURIComponent(message);
  const disabled = message.length === 0 || encodedMessage.length > mastodonEncodedLengthLimit;
  const [mastodonDialogOpen, setMastodonDialogOpen] = useState(false);
  const [mastodonInstance, setMastodonInstance] = useState(localStorage.mastodonInstance ?? defaultMastodon);
  const [lastAction, setLastAction] = useState('');

  const open = () => {
    setLastAction('open');
    setMastodonDialogOpen(true);
  }

  const post = () => {
    setLastAction('post');
    setMastodonDialogOpen(true);
  }

  const closeMastodonDialog = () => {
    setMastodonDialogOpen(false);
  }

  const openMastodon = (instance) => {
    setMastodonInstance(instance);
    localStorage.mastodonInstance = instance;
    if (lastAction === 'post') {
      const text = encodeURIComponent(message);
      const url = `${instance}/share?text=${text}`
      openUrl(url);
    } else {
      openUrl(instance);
    }
  }

  const OpenButton = () => {
    return (
      <div>
        <Button onClick={open}>
          Open
        </Button>
        <MastodonDialog
          open={mastodonDialogOpen}
          onClose={closeMastodonDialog}
          onConfirm={openMastodon}
          mastodonInstance={mastodonInstance}
          >
        </MastodonDialog>
      </div>
    )
  }

  return (
    <SNSTableRow title="Mastodon"
      openButton={(<OpenButton/>)}

      postButton={
        (<Button onClick={post} disabled={disabled}>
          Post
        </Button>)
      }
    >
    </SNSTableRow>
  )
}

export default MastodonRowButtons;
