import React from 'react';
import SNSTableRow from './SNSTableRow.js';
import Button from '@material-ui/core/Button';
import {openUrl} from './Browser';

function PlurkRowButtons({message = ''}) {

  const disabled = message.length === 0;

  const open = () => {
    openUrl('https://plurk.com/');
  }

  const post = () => {
    const text = encodeURIComponent(message);
    const url = `http://plurk.com/?qualifier=shares&status=${text}`

    openUrl(url);
  }

  return (
    <SNSTableRow title="Plurk"
      openButton={
      (<Button onClick={open}>
        Open
      </Button>)}

      postButton={
        (<Button onClick={post} disabled={disabled}>
          Post
        </Button>)
      }

    >
    </SNSTableRow>
  )
}

export default PlurkRowButtons;
