import React, {useState} from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';

const MastodonDialog = ({open, onClose, onConfirm, mastodonInstance}) => {

  const [instance, setInstance] = useState(mastodonInstance);

  const confirm = () => {
    onConfirm(instance);
    onClose();
  }

  const handleChange = (event) => {
    setInstance(event.target.value);
  }

  return (
    <Dialog open={open} onClose={onClose}>
      <DialogTitle>Mastodon Instance Address</DialogTitle>
      <DialogContent>
        <TextField
          autoFocus
          margin="dense"
          id="name"
          label="Address"
          type="email"
          defaultValue={mastodonInstance}
          onChange={handleChange}
          fullWidth
        />
      </DialogContent>
      <DialogActions>
        <Button onClick={onClose} color="primary">
          Cancel
        </Button>
        <Button onClick={confirm} color="primary">
          Confirm
        </Button>
      </DialogActions>
    </Dialog>
  )
}

export default MastodonDialog;
