import React from 'react';
import SNSTableRow from './SNSTableRow.js';
import Button from '@material-ui/core/Button';
import {openUrl} from './Browser';

function TwitterRowButtons({message = ''}) {

  const disabled = message.length === 0;

  const open = () => {
    openUrl('https://twitter.com');
  }

  const post = () => {
    const text = encodeURIComponent(message);
    const url = `https://twitter.com/intent/tweet?text=${text}`
    openUrl(url);
  }

  return (
    <SNSTableRow title="Twitter"
      openButton={
      (<Button onClick={open}>
        Open
      </Button>)}

      postButton={
        (<Button onClick={post} disabled={disabled}>
          Post
        </Button>)
      }

    >
    </SNSTableRow>
  )
}

export default TwitterRowButtons;
