import React, {useState} from 'react';
import Box from '@material-ui/core/Box';
import makeStyles from '@material-ui/core/styles/makeStyles';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';
import WordCounter from './WordCounter';
import IconButton from '@material-ui/core/IconButton';
import FileCopyIcon from '@material-ui/icons/FileCopy';
import SNSTable from './SNSTable';

const useStyles = makeStyles((theme) => ({
  paper: {
    padding: '20px',
    margin: '0px',
    maxWidth: '640px',
  },
  full: {
    width: '100%',
    height: '100%',
  },
  content: {
    width: '100%',
    height: '100%',
    backgroundColor: theme.palette.background.default,
  },
  page: {
    width: '100vw',
    height: '100vh'
  },
  appBar: {
    position: 'relative',
    margin: 0,
    padding: 0,
  },
  textField: {
    marginBottom: theme.spacing(1)
  }
}));

const Content = ({children}) => {
  const classes = useStyles();

  return (
    <Box display="flex" justifyContent="center" className={classes.content}>
      <Box flexGrow={1} clone>
          <Paper className={classes.paper}>
            {children}
          </Paper>
        </Box>
    </Box>
  )
}

function App() {
  const classes = useStyles();

  const [message, setMessage] = useState(localStorage.message ?? '');

  const handleChange = (event) => {
    setMessage(event.target.value);
    localStorage.message = event.target.value;
  };

  const copy = () => {
    const tmp = document.createElement("input");
    tmp.style = "position: absolute; left: -1000px; top: -1000px";
    tmp.value = message;
    document.body.appendChild(tmp);
    tmp.select();
    document.execCommand("copy");
    document.body.removeChild(tmp);
  }

  return (
    <Content>
      <Box display="flex" flexDirection="column" className={classes.full}>

        <Box display='flex'  alignItems="center" >
          <Typography gutterBottom display="inline" variant="h5">
            PostToX
          </Typography>
        </Box>

        <Typography gutterBottom variant="subtitle1">Write something and post to X SNS platforms</Typography>
        <Box clone>
          <TextField
            label="Message"
            multiline
            variant="filled"
            rows={10}
            onChange={handleChange}
            defaultValue={message}
            className={classes.textField}
          />
        </Box>
        <Box display="flex" alignItems="flex-start">
          <IconButton aria-label="Copy" onClick={copy}>
            <FileCopyIcon/>
          </IconButton>

          <Box flexGrow={1}></Box>
          <WordCounter message={message}></WordCounter>
        </Box>

        <SNSTable message={message}>
        </SNSTable>
      </Box>
    </Content>
  );
}

export default App;
